<template>
  <div class="section character">
    <faction-logo ref="factionCom"></faction-logo>
    <slide :character="character" :key="character._id" class="slide" v-for="character in characters" />
    <btn-next></btn-next>
  </div>
</template>

<script>
  import Slide from '../components/character/Slide'
  import FactionLogo from '../components/character/FactionLogo'
  import BtnNext from '../components/BtnNext'

  export default {
    data() {
      return {
        characters: [],
      }
    },
    props: ['reference', 'activeIndex'],
    methods: {
      async getCharacters() {
        const res = await this.$http.get('/web/api/character')
        this.characters = res.data
      }
    },
    components: { Slide, FactionLogo, BtnNext },
    created() {
      this.getCharacters()
    },
    updated() {
      //  fullpage  DOM
      //  slide fullpage  DOM
      //  this.$parent fullpage  DOM
      this.reference.fullpage.build()
    },
    watch: {
      characters() {
        // 
        this.$refs.factionCom.faction = this.characters[this.activeIndex].faction
      },
      activeIndex() {
        // 
        this.$refs.factionCom.faction = this.characters[this.activeIndex].faction
      }
    }
  }
</script>

<style lang="scss">
  .character {
    position: relative;
    background: url('../assets/images/character_bg.jpg') no-repeat bottom / cover;

    .fp-controlArrow.fp-prev {
      border-width: 10px 20px 10px 0 !important;
    }

    .fp-controlArrow.fp-next {
      border-width: 10px 0 10px 20px !important;
    }
  }
</style>